@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

html, 
body {
  color: #000000;
}

h1 {
  display: flex;
  font-style: italic, bold;
  font-size: 3rem;
  font-weight: 900;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

h2 {
  display: flex;
  font-size: 2.2rem;
  font-weight: 900;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;

}
.body {
  display: flex;
  font-weight: 500;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

ul {
  white-space: pre-wrap;
  margin-left: -5rem
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: black;
  height: 90vh;
}

label {
  font-size: 1.5rem;
}

.slide {
  align-self: center;
  align-items: center;
  align-content: center;
}

iframe { 
  display: block; margin: 0 auto; 
  resize: both;
}

textarea {
  width: 100%;
  height: 2000px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
  resize: vertical;
  overflow: auto;
}